import Container from 'react-bootstrap/Container'
import { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import Web3 from 'web3'
import { ethers } from 'ethers'
import {
  accountSave,
  accountRemove,
  contractABIFromAddress,
  mintDataSave,
} from '../action/account.action'
import { Modal, Button } from 'react-bootstrap'
import { errorOccured, successOccured } from '../action/error.action'
import env from '../env.json'
const web3 = new Web3(new Web3.providers.HttpProvider(env.API_KEY))

function Home() {
  const user = useSelector((state) => state.auth.user)
  const accounts = useSelector((state) => state.account.accountList)
  const contractABI = useSelector((state) => state.account.contractABI)
  const contractFunction = useSelector(
    (state) => state.account.contractFunction,
  )
  const boolVariables = useSelector((state) => state.account.boolVariables)
  const monitorFunctions = useSelector(
    (state) => state.account.monitorFunctions,
  )

  const [show, setShow] = useState(false)
  const [value, SetValue] = useState(0)
  const [gasPriceAuto, SetGasPriceAuto] = useState(true)
  const [gasPrice, SetGasPrice] = useState(30)
  const [gasLimitAuto, SetGasLimitAuto] = useState(true)
  const [gasLimit, SetGasLimit] = useState(30000)
  // const [tokenPrice, SetTokenPrice] = useState(0)
  // const [tokenAmount, SetTokenAmount] = useState(0)
  const [monitorMethod, SetMonitorMethod] = useState('time')
  const [monitorFunction, SetMonitorFunction] = useState('')
  const [sendDate, SetSendDate] = useState('2022-7-15')
  const [sendTime, SetSendTime] = useState('00:00:00')
  const [privateKey, SetPrivateKey] = useState('0x')
  const [boolValue, SetBoolValue] = useState('None')
  const [contractAddress, SetContractAddress] = useState('0x')
  const [mintFunction, SetMintFunction] = useState('')
  const [mintParameters, SetMintParameters] = useState([])
  const [importKey, SetImportKey] = useState('0x')
  const dispatch = useDispatch()

  useEffect(() => {
    let now = new Date()
    let year = now.getFullYear()
    let month =
      now.getMonth() + 1 > 9
        ? now.getMonth() + 1
        : '0' + (now.getMonth() + 1).toString()
    let day = now.getDate() > 9 ? now.getDate() : '0' + now.getDate().toString()
    let hour =
      now.getHours() > 9 ? now.getHours() : '0' + now.getHours().toString()
    let minute =
      now.getMinutes() > 9
        ? now.getMinutes()
        : '0' + now.getMinutes().toString()
    let second =
      now.getSeconds() > 9
        ? now.getSeconds()
        : '0' + now.getSeconds().toString()
    let sendDate = `${year}-${month}-${day}`
    let sendTime = `${hour}:${minute}:${second}`
    SetSendDate(sendDate)
    SetSendTime(sendTime)
  }, [])

  const handleClose = () => setShow(false)
  const handleShow = () => setShow(true)
  const onSetGasPrice = (e) => {
    try {
      let gasPrice = e.target.value
      SetGasPrice(gasPrice)
    } catch (err) {
      dispatch(errorOccured(err))
    }
  }

  const onSetGasLimit = (e) => {
    try {
      let gasLimit = Number(e.target.value)
      SetGasLimit(gasLimit)
    } catch (err) {
      dispatch(errorOccured(err))
    }
  }

  const onCreateWallet = (e) => {
    let newAccount = web3.eth.accounts.create()
    let data = { _id: user._id, privateKey: newAccount.privateKey }
    dispatch(accountSave(data))
  }

  const onAccountAddressChange = (e) => {
    try {
      let currentAddress = e.target.value
      if (currentAddress.includes('0x') && currentAddress !== '0x') {
        for (let i = 0; i < accounts.length; i++) {
          let privateKey = accounts[i]
          let account = web3.eth.accounts.privateKeyToAccount(privateKey)
          if (account.address === currentAddress) {
            SetPrivateKey(privateKey)
            let provider = new ethers.providers.WebSocketProvider(env.WSS_KEY)
            let wallet = new ethers.Wallet(privateKey, provider)
            wallet.connect(provider)
          }
        }
      }
    } catch (err) {
      dispatch(errorOccured(err))
    }
  }

  const onSearchABIFromAddress = () => {
    dispatch(contractABIFromAddress({ address: contractAddress }))
  }

  // const onSetTokenPrice = async (e) => {
  //   try {
  //     SetTokenPrice(e.target.value)
  //     const gasPrice = await web3.eth.getGasPrice()
  //     SetGasPrice(gasPrice / Math.pow(10, 9))
  //   } catch (err) {
  //     dispatch(errorOccured(err))
  //   }
  // }

  // const onSetTokenAmount = async (e) => {
  //   try {
  //     SetTokenAmount(e.target.value)
  //     const gasPrice = await web3.eth.getGasPrice()
  //     SetGasPrice(gasPrice / Math.pow(10, 9))
  //   } catch (err) {
  //     dispatch(errorOccured(err))
  //   }
  // }

  const onMintNFT = async () => {
    try {
      let account = web3.eth.accounts.privateKeyToAccount(privateKey)
      console.log('account', account)
      let publicKey = account.address
      let amount = await web3.eth.getBalance(account.address)
      if (parseInt(amount) < gasLimit * gasPrice * Math.pow(10, 9)) {
        dispatch(errorOccured('Not Enough Ethers In Your Wallet\n'))
        return
      }
      const nonce = await web3.eth.getTransactionCount(publicKey, 'latest')
      let NFTContract = new web3.eth.Contract(
        contractABI,
        contractAddress,
        account,
      )

      //the transaction
      const tx = {
        from: publicKey,
        to: contractAddress,
        nonce: nonce,
        gas: gasLimit,
        maxPriorityFeePerGas: parseInt(gasPrice * Math.pow(10, 9)),
        value: value.toString() + '000000000',
        data: NFTContract.methods[mintFunction](
          ...mintParameters.map((param) => Number(param)),
        ).encodeABI(),
      }
      const signPromise = web3.eth.accounts.signTransaction(tx, privateKey)
      if (monitorMethod === 'time') {
        signPromise
          .then((signedTx) => {
            let time = new Date(sendDate + ' ' + sendTime)
            let now = new Date()
            if (Math.abs(time - now) > 60 * 1000) {
              // 1 minute
              let data = {
                _id: user._id,
                account: account.address,
                time,
                rawTransaction: signedTx.rawTransaction,
                monitorMethod,
                monitorFunction,
              }
              dispatch(mintDataSave(data))
            } else {
              web3.eth.sendSignedTransaction(signedTx.rawTransaction, function (
                err,
                hash,
              ) {
                if (!err) {
                  dispatch(
                    successOccured(
                      'The hash of your transaction is: \n' +
                        hash +
                        "\nCheck etherscan's Mempool to view the status of your transaction!",
                    ),
                  )
                } else {
                  dispatch(
                    errorOccured(
                      'Something went wrong when submitting your transaction.\n' +
                        err,
                    ),
                  )
                }
              })
            }
          })
          .catch((err) => {
            console.log('Promise failed:' + err)
            dispatch(errorOccured(err))
          })
      }
      if (monitorMethod === 'function') {
        signPromise
          .then((signedTx) => {
            let now = new Date()
            let data = {
              _id: user._id,
              account: account.address,
              time: now,
              rawTransaction: signedTx.rawTransaction,
              monitorMethod,
              monitorFunction,
            }
            dispatch(mintDataSave(data))
          })
          .catch((err) => {
            console.log('Promise failed:' + err)
          })
      }
    } catch (err) {
      dispatch(errorOccured(err))
    }
  }

  const onChangeBoolVariable = async (e) => {
    let NFTContract = new web3.eth.Contract(contractABI, contractAddress)

    const res = await NFTContract.methods.mintEnabled().call()

    if (res) {
      SetBoolValue('True')
    } else {
      SetBoolValue('False')
    }
  }

  const onSelectMonitorFunction = (e) => {
    SetMonitorFunction(e.target.value)
  }

  const onRemoveWallet = () => {
    if (privateKey.includes('0x') && privateKey !== '0x') {
      let data = { _id: user._id, privateKey }
      dispatch(accountRemove(data))
    }
  }

  const handleImport = () => {
    try {
      web3.eth.accounts.privateKeyToAccount(importKey)
      let data = { _id: user._id, privateKey: importKey }
      dispatch(accountSave(data))
    } catch {
    } finally {
      setShow(false)
    }
  }
  const onChangeImportKey = (e) => {
    SetImportKey(e.target.value)
  }

  const onChangeMonitorMethod = (e) => {
    SetMonitorMethod(e.target.value)
  }

  const onChangeParameter = (index) => (e) => {
    mintParameters[index] = e.target.value
    console.log(mintParameters)
    SetMintParameters([...mintParameters])
  }

  const onSelectMintFunction = (e) => {
    SetMintFunction(e.target.value)
    SetMintParameters(
      Array(
        contractABI.find((func) => func.name === e.target.value).inputs.length,
      ),
    )
  }
  return (
    <div className="body-padding">
      <div bg="dark" variant="light">
        <Container>
          <div className="top-text-group">
            <div className="d-flex justify-content-left">
              <div className="text-white px-3 pt-4 h1 flex-grow-1 font-bold-poppins">
                Win NFT Trading
              </div>
              <div className="text-white pt-4 h4 text-end d-flex flex-column ">
                {user ? (
                  <>
                    <button
                      type="button"
                      className="btn btn-success rounded-pill create-button mb-3"
                      onClick={onCreateWallet}
                    >
                      {' '}
                      Create Wallet
                    </button>
                    <button
                      type="button"
                      className="btn btn-success rounded-pill create-button mb-3"
                      onClick={onRemoveWallet}
                    >
                      {' '}
                      Remove Wallet
                    </button>
                    <button
                      type="button"
                      className="btn btn-success rounded-pill create-button mb-3"
                      data-toggle="modal"
                      data-target="#exampleModal"
                      onClick={handleShow}
                    >
                      {' '}
                      Import Wallet
                    </button>
                  </>
                ) : null}
              </div>
            </div>
            <div className="text-white px-3 h6 text-start ">
              High-performance network for the fastest NFT trading
            </div>
          </div>
          <div className="account-address-group">
            <div className="text-white px-3 spacing-medium h5 text-start">
              <div className="font-bold-poppins">
                <strong>Account Address</strong>
              </div>
              <div className="horizontal-spliter"></div>
              <div>Select Account Address</div>
            </div>
            <div className="account-address-box mx-3">
              <select
                className="select-account-address"
                onChange={onAccountAddressChange}
                defaultValue="default"
              >
                <option value={'default'}>Account Address</option>
                {accounts.length > 0
                  ? accounts
                      .filter((account) => account !== null)
                      .map((value, key) => {
                        let account = web3.eth.accounts.privateKeyToAccount(
                          value,
                        )
                        return (
                          <option key={key} value={account.address}>
                            {account.address}
                          </option>
                        )
                      })
                  : null}
              </select>
              <h3></h3>
              <p style={{ color: 'white', marginTop: '5px' }}>
                private key : {privateKey}
              </p>
              {/* <div className='text-white px-3 h4 text-end d-inline'>
                            {user ? <button type="button" className="btn btn-primary rounded-pill connect-button" onClick={onConnectWallet}> Connect Wallet</button> : null}
                        </div> */}
            </div>
          </div>
          <div className="nft-address-group">
            <div className="text-white px-3 spacing-medium h5 text-start">
              <div className="font-bold-poppins">
                <strong>NFT Minting</strong>
              </div>
              <div className="horizontal-spliter"></div>
              <div>NFT Contract Address</div>
            </div>
            <div className="nft-address-box mx-3">
              <input
                type="text"
                value={contractAddress}
                onChange={(e) => SetContractAddress(e.target.value)}
              ></input>
              <button
                className="nft-address-search"
                onClick={onSearchABIFromAddress}
              >
                <img
                  alt="IMG"
                  src="data:image/png;base64,"
                ></img>
              </button>
            </div>
          </div>
          <div className="mint-address-group w-50">
            <div className="text-white px-3 spacing-medium h5 text-start">
              <div className="font-bold-poppins">
                <strong>Select Contract Mint Function</strong>
              </div>
            </div>
            <div className="account-address-box mx-3">
              <select
                className="select-account-address w-100"
                onChange={onSelectMintFunction}
              >
                <option>Select Mint Function</option>
                {contractFunction
                  ? contractFunction.map((value, key) => {
                      return (
                        <option value={value.name} key={key}>
                          {value.name}
                        </option>
                      )
                    })
                  : null}
              </select>
              <div className="mt-2">
                <span className="text-white p-0 font-size-2">
                  {'Value To Send (GWei)'}
                </span>
                <div className="d-flex bg-dark rounded-5">
                  <input
                    type="number"
                    className="w-80"
                    min={0}
                    value={value}
                    onChange={(e) => SetValue(e.target.value)}
                  />
                </div>
              </div>
              {contractABI.length && mintFunction !== '' ? (
                contractABI
                  .find((func) => func.name === mintFunction)
                  .inputs.map((input, index) => {
                    return (
                      <div key={index} className="mt-2">
                        <span className="text-white p-0">{`${input.name} (${input.type})`}</span>
                        <div className="d-flex bg-dark rounded-5">
                          <input
                            type="text"
                            className="w-80"
                            onChange={onChangeParameter(index)}
                          />
                        </div>
                      </div>
                    )
                  })
              ) : (
                <></>
              )}
            </div>

            {/* <div className="account-address-box mx-3">
              <select
                className="select-account-address w-100"
                onChange={onChangeBoolVariable}
              >
                <option>Select Bool Variable</option>
                {boolVariables
                  ? boolVariables.map((value, key) => {
                      return (
                        <option value={value.name} key={key}>
                          {value.name}
                        </option>
                      )
                    })
                  : null}
              </select>
            </div> */}
            {/* <p style={{ color: 'white', marginTop: '5px', marginLeft: '20px' }}>
              value : {boolValue}
            </p> */}
            {/* <div className="d-flex mt-20 mx-3">
              <div className="px-2 d-inline-block w-30">
                <div>
                  <span className="text-white p-0">Price</span>
                  <div className="d-flex bg-dark rounded-5">
                    <input
                      type="text"
                      className="w-80"
                      value={tokenPrice}
                      onChange={onSetTokenPrice}
                    />
                    <img
                      alt="IMG"
                      className="p-2"
                      src="data:image/png;base64,"
                    />
                  </div>
                </div>
              </div>
              <div className="px-2 d-inline-block w-30">
                <div>
                  <span className="text-white p-0">of Token</span>
                  <div className="d-flex bg-dark rounded-5">
                    <input
                      type="text"
                      className="w-80"
                      value={tokenAmount}
                      onChange={onSetTokenAmount}
                    />
                    <img
                      alt="IMG"
                      className="p-2"
                      src="data:image/png;base64,"
                    />
                  </div>
                </div>
              </div>
              <div className="px-2 d-inline-block w-30">
                <div>
                  <span className="text-white p-0">Total Price</span>
                  <div className="d-flex rounded-5">
                    <input
                      type="text"
                      className="w-80 black-back text-right"
                      disabled={true}
                      value={tokenAmount * tokenPrice}
                      onChange={() => {}}
                    ></input>
                    <img
                      alt="IMG"
                      className="p-2"
                      src="data:image/png;base64,"
                    />
                  </div>
                </div>
              </div>
            </div> */}
          </div>
          <div className="d-flex w-100">
            <div className="transaction-gas-group mx-3 font-bold-poppins w-50">
              <div className="h4 text-white">Transaction and Gas Summary</div>
              <div className="pt-20">
                <div className="d-flex text-white gas-price-limit-padding">
                  <div className="flex-grow-1 p-2">Gas Price:</div>
                  <button
                    type="button"
                    className={
                      gasPriceAuto
                        ? 'btn btn-primary rounded-pill font-bold-poppins auto-button'
                        : 'btn btn-secondary rounded-pill font-bold-poppins auto-button'
                    }
                    onClick={() => SetGasPriceAuto(!gasPriceAuto)}
                  >
                    Auto
                  </button>
                  <div className="d-flex bg-dark rounded-3 gas-button-group">
                    <input
                      type="text"
                      disabled={gasPriceAuto ? true : false}
                      value={gasPrice}
                      onChange={onSetGasPrice}
                    ></input>
                    <span className="p-2">GWEI</span>
                  </div>
                </div>
              </div>
              <div className="pt-30">
                <div className="d-flex text-white gas-price-limit-padding">
                  <div className="flex-grow-1 p-2">Gas Limit:</div>
                  <button
                    type="button"
                    className={
                      gasLimitAuto
                        ? 'btn btn-primary rounded-pill font-bold-poppins auto-button'
                        : 'btn btn-secondary rounded-pill font-bold-poppins auto-button'
                    }
                    onClick={() => SetGasLimitAuto(!gasLimitAuto)}
                  >
                    Auto
                  </button>
                  <div className="d-flex bg-dark rounded-3 gas-button-group">
                    <input
                      type="text"
                      disabled={gasLimitAuto ? true : false}
                      placeholder={250000}
                      value={gasLimit}
                      onChange={onSetGasLimit}
                    ></input>
                  </div>
                </div>
              </div>
            </div>
            <div className="transaction-gas-group text-white align-middle mx-5 font-bold-poppins w-50">
              {/* <div className="h4 text-white pb-3">Mint Time</div> */}
              <div className="form-check">
                <input
                  className="form-check-input"
                  type="radio"
                  name="flexRadioDefault"
                  id="flexRadioDefault1"
                  value="time"
                  onChange={onChangeMonitorMethod}
                  checked={monitorMethod === 'time'}
                />
                <label
                  className="form-check-label h4 text-white pb-3"
                  forhtml="flexRadioDefault1"
                >
                  Mint Time
                </label>
              </div>
              <div className="d-flex text-white">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="50"
                  height="50"
                  fill="currentColor"
                  className="bi bi-stopwatch"
                  viewBox="0 0 16 16"
                >
                  <path d="M8.5 5.6a.5.5 0 1 0-1 0v2.9h-3a.5.5 0 0 0 0 1H8a.5.5 0 0 0 .5-.5V5.6z" />
                  <path d="M6.5 1A.5.5 0 0 1 7 .5h2a.5.5 0 0 1 0 1v.57c1.36.196 2.594.78 3.584 1.64a.715.715 0 0 1 .012-.013l.354-.354-.354-.353a.5.5 0 0 1 .707-.708l1.414 1.415a.5.5 0 1 1-.707.707l-.353-.354-.354.354a.512.512 0 0 1-.013.012A7 7 0 1 1 7 2.071V1.5a.5.5 0 0 1-.5-.5zM8 3a6 6 0 1 0 .001 12A6 6 0 0 0 8 3z" />
                </svg>
                <div className="d-flex pt-2 timer-style">
                  <input
                    type="date"
                    value={sendDate}
                    disabled={monitorMethod !== 'time'}
                    onChange={(e) => SetSendDate(e.target.value)}
                  ></input>
                  <input
                    type="time"
                    value={sendTime}
                    disabled={monitorMethod !== 'time'}
                    onChange={(e) => SetSendTime(e.target.value)}
                  ></input>
                </div>
              </div>
              <div className="form-check pt-3">
                <input
                  className="form-check-input"
                  type="radio"
                  name="flexRadioDefault"
                  id="flexRadioDefault1"
                  value="function"
                  onChange={onChangeMonitorMethod}
                  checked={monitorMethod === 'function'}
                />
                <label
                  className="form-check-label h4 text-white pb-3"
                  forhtml="flexRadioDefault1"
                >
                  Monitor Function
                </label>
                <div>
                  <select
                    className="select-account-address w-100"
                    disabled={monitorMethod !== 'function'}
                    onChange={onSelectMonitorFunction}
                  >
                    <option>Select Monitor Function</option>
                    {monitorFunctions
                      ? monitorFunctions.map((value, key) => {
                          return (
                            <option value={value.name} key={key}>
                              {value.name}
                            </option>
                          )
                        })
                      : null}
                  </select>
                </div>
              </div>
            </div>
          </div>
          <div className="w-50 d-flex justify-content-center">
            <button
              className="btn btn-primary w-75 rounded-2 h4 font-bold-poppins buy-nft-button"
              onClick={onMintNFT}
            >
              MINT
            </button>
          </div>
        </Container>
      </div>
      <Modal show={show} onHide={handleClose}>
        <Modal.Header>
          <Modal.Title>Input PrivateKey</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <input
            placeholder="0x"
            style={{ width: '100%' }}
            onChange={onChangeImportKey}
          ></input>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={handleImport}>
            Import
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  )
}

export default Home
